
#app {
        height: 100%;
}
#app .wrapBox {
        height: 100%;
}
#app .wrapBox>.header {
        height: 61px;
        width: 100%;
        background-color: #4d4d4d;
        position: absolute;
        z-index: 1000;
        min-width:768px;
}
#app .reconciliation {
        height: 100%;
        width: 100%;
        position: absolute;
        top: 0;
}
.reconciliation {
        position: absolute;
        top: 61px;
}
.header>img {
        position: absolute;
        left: 6px;
        top: 0px;
        width: 97px;
        height: 61px;
}
.header>.title {
        position: absolute;
        left: 113px;
        top: 27px;
        width: 120px;
        height: 25px;
        font-family: 'PingFangSC-Semibold', 'PingFang SC Semibold', 'PingFang SC';
        font-weight: 650;
        font-style: normal;
        color: #FFFFFF;
        font-size: 18px;
        font-weight: bold;
}
.header>.logout {
        position: absolute;
        right: 5%;
        top: 17px;
        width: 80px;
        height: 32px;
        font-family: 'PingFangSC-Regular', 'PingFang SC';
        font-weight: 400;
        font-style: normal;
        color: #FFFFFF;
        background-color: #d8534e;
        text-align: center;
        line-height: 32px;
        font-size: 13px;
        cursor: pointer;
        border-radius: 5px;
}

    

    
    /*左边导航*/
.leftNav {
        width: 15%;
        height: 100%;
        min-width:150px;
        min-height:500px;
        background-color: #202020;
        position: absolute;
        left: 0;
        box-sizing: border-box;
        padding-top: 61px;
        z-index: 10;
}
.leftNav ul {
        border: none;
        margin:0;
        padding:0;
}
.leftNav li {
        position:relative;
        background-color: #202020;
        height: 61px;
        line-height: 61px;
        text-align: center;
        border-bottom: 1px solid #3e3e3e;
}
.leftNav li img{
        position:absolute;
        height:20px;
        top:50%;
        left:15%;
        transform: translateY(-50%);
}
.leftNav li a {
        width: 100%;
        height: 100%;
        display: block;
        border-radius: 0%;
        color: #fff;
        text-align: center;
        transition: all 0.5s ease 0s;
        text-decoration: none;
}
.leftNav a.active {
        color: red;
        background-color: #fdfdfd;
        font-weight: 700;
}
.wrapBox .leftNav li:hover a {
        background-color: #fff;
        color: #000;
}
    
    
    /*右边内容*/
.rightContent {
        background-color: #fdfdfd;        
        /*position: absolute;*/
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        padding-left: 16%;
        padding-top: 81px;
        padding-right: 10px;
        background:#f0f0f0;
        min-width:768px;
        min-height:500px;
        overflow:auto;
}
@media screen and (max-width:1000px){
.rightContent{ 
            padding-left: 160px;
}
}
    /*载入动画*/
.fade-enter-active {
        transition: opacity .5s
}
.fade-enter {
        opacity: 0
}

    /*表格 */
table {
