    /*登录界面样式·*/
.loginPage {
        width: 100%;
        height: 100%;
        transition: all 1s ease 0s;
}
.loginInfo {
        width: 50%;
        height: 50%;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        font-size: 50px;
        text-align: center;
        /*background-color: rgba(0, 0, 0, .7);*/
}
.loginBox {
        width: 100%;
        background-color: rgba(0, 0, 0, 0);
        text-align: center;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        color: #fff;
        font-size: 20px;
        border-radius: 5%;
}
.loginBox form div {
        position: relative;
}
    /*表单验证提示*/
.loginBox form span {
        position: absolute;
        top: 50%;
        margin-left:150px;
        font-size: 12px;
        color: red;
        font-weight: 700;
        /*transform: translateY(-50%)*/
}
.loginBox>form input {
        width: 270px;
        height: 42px;
        margin-top: 25px;
        padding: 0 15px;
        background: #2d2d2d;
        background: rgba(45, 45, 45, .15);
        border-radius: 6px;
        border: 1px solid #3d3d3d;
        border: 1px solid rgba(255, 255, 255, .15);
        -webkit-box-shadow: 0 2px 3px 0 rgba(0, 0, 0, .1) inset;
        font-family: 'PT Sans', Helvetica, Arial, sans-serif;
        font-size: 14px;
        color: #fff;
        text-shadow: 0 1px 2px rgba(0, 0, 0, .1);
        -webkit-transition: all .2s;
        outline: none;
        margin:15px auto;
}
.loginBox>form input:focus{
        color:#000;
        background:pink;
}
.loginBox .loginBtn {
        box-shadow:  5px #888888;
        background: -webkit-linear-gradient(top, #ff730e, #ef4300, #ff730e);
}
.loginBox .loginBtn:hover {
        background: -webkit-linear-gradient(top, #ef4300, #ef4300, #ef4300);
}
.loginBox .loginBtn:active {
        background: #ccc;
}
    /*placeholder样式*/
input::-webkit-input-placeholder {
        /* WebKit browsers */
        color: #fff;
}
input:-moz-placeholder {
        /* Mozilla Firefox 4 to 18 */
        color: #fff;
}
input::-moz-placeholder {
        /* Mozilla Firefox 19+ */
        color: #fff;
}
input:-ms-input-placeholder {
        /* Internet Explorer 10+ */
        color: #fff;
}



